import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cat_id: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			kw: '',
			options: {},
			page: 1
		};
	},
	mounted() {



	},
	onLoad(options){



		this.cat_id = options.cat_id;
		this.kw = options.kw || ''
		this.options = options
		this.loadData();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
		this.page = 1
	},
	//下拉刷新
	onPullDownRefresh(){
		this.page = 1
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.page ++;
		this.loadData();
	},
	methods: {
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			let form = {
				cat_id: this.cat_id || 0,
				sort: this.filterIndex,
				priceOrder: this.priceOrder,
				kw: this.kw,
				page: this.page
			}
			let res;
			if (this.$tools.has_addon('xshopgroupon') && this.options.groupon == 1) {
				form.groupon = 1
				res = await this.$http.get('groupon.product.list', form)
			} else {
				res = await this.$http.get('product.list', form);
			}
			this.page += 1;
			let goodsList = res.data.data
			if(type === 'refresh'){
				this.goodsList = [];
			}
			
			this.goodsList = this.goodsList.concat(goodsList);
			
			this.loadingType = res.data.current_page >= res.data.last_page ? 'nomore' : 'more';
			if(type === 'refresh'){
				if(loading == 1){
					uni.hideLoading()
				}else{
					uni.stopPullDownRefresh();
				}
			}
		},
		//筛选点击
		tabClick(index){
			this.page = 1
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.cat_id = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent(){}
	},
}
